import React from 'react'
import Navigation from './components/Navigation'
import ToDoContainer from './components/ToDoContainer'
import ToDonesContainer from './components/ToDonesContainer'
import './css/App.scss'
import {HashRouter, Route, Switch} from 'react-router-dom'
import About from './components/About'
import NotFound from './components/NotFound'

// localStorage.setItem('to-do-app', 'abcd')
// localStorage.getItem('to-do-app')
class App extends React.Component{

  state={
    todoItems:[        
    ]
  }

  componentDidMount(){
    let data = localStorage.getItem('todoapp')
    if(data){
      let convertedData = JSON.parse(data)
      this.setState({
      todoItems:convertedData
      })
    }   
  }

  addItem=(value)=>{
    let item = {id:this.state.todoItems.length, text:value, done:false}
    let copystate = [...this.state.todoItems]
    copystate.push(item)
    this.setState({
      todoItems:copystate
    })
    localStorage.setItem('todoapp', JSON.stringify(copystate))
    /*this.setState({
      todoItems:[...this.state.todoItems, item]
    })*/
  }

  updateItem=(id)=>{
    let updatedItems = this.state.todoItems.map(item=>{
      if(item.id===id){
        item.done=!item.done
        return item
      }else{
        return item
      }
    })
    this.setState({
      todoItems:updatedItems
    })
    localStorage.setItem('todoapp', JSON.stringify(this.state.todoItems))

  }

  deleteItem=(id)=>{
    let updatedData = this.state.todoItems.filter(item=>item.id!==id)
    this.setState({
      todoItems:updatedData
    })
    localStorage.setItem('todoapp', JSON.stringify(this.state.todoItems))
  }

  render(){
    let toDos = this.state.todoItems.filter(item=>!item.done)
    let toDones = this.state.todoItems.filter(item=>item.done)

    return (
      <HashRouter>
        <div className="app">
          <Navigation/>

          <Switch>
            <Route exact path='/'>
              <ToDoContainer 
                toDos={toDos} addItem={this.addItem} 
                updateItem={this.updateItem} 
                deleteItem={this.deleteItem}/>
              <ToDonesContainer 
                toDones={toDones} 
                updateItem={this.updateItem} 
                deleteItem={this.deleteItem}/>
            </Route>
            <Route path='/about' component={About}/>
            <Route component={NotFound}/>
          </Switch>
        </div>
      </HashRouter>
    );
  }
  
}

export default App;
